@import url("https://rsms.me/inter/inter-ui.css");
.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

@media (prefers-reduced-motion: reduce) {
  * {
    animation: none !important;
    transition: none !important; } }

html {
  position: relative;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-size-adjust: 100%;
  text-rendering: optimizeLegibility; }

html,
body {
  height: 100%;
  width: 100%;
  overflow: hidden; }

input,
textarea,
button,
select,
a,
div {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

body {
  font-family: 'Inter UI', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif; }

@supports (font-variation-settings: normal) {
  body {
    font-family: 'Inter UI var alt', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif; } }

.TopBar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 4rem;
  padding: 0.5rem 1.5rem;
  position: relative; }
  .TopBar::after {
    content: ' ';
    height: 2px;
    background: #e7eef3;
    position: absolute;
    bottom: 0;
    width: calc(100% - 3rem);
    left: 50%;
    transform: translateX(-50%); }
  .TopBar .IconList {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    width: 12rem; }
  .TopBar .Button {
    width: 12rem;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden; }
