@import url("https://rsms.me/inter/inter-ui.css");
.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

@media (prefers-reduced-motion: reduce) {
  * {
    -webkit-animation: none !important;
            animation: none !important;
    transition: none !important; } }

html {
  position: relative;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
      -ms-text-size-adjust: 100%;
          text-size-adjust: 100%;
  text-rendering: optimizeLegibility; }

html,
body {
  height: 100%;
  width: 100%;
  overflow: hidden; }

input,
textarea,
button,
select,
a,
div {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

body {
  font-family: 'Inter UI', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif; }

@supports (font-variation-settings: normal) {
  body {
    font-family: 'Inter UI var alt', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif; } }

.small {
  width: 1rem; }

.medium {
  width: 1.5rem; }

.large {
  width: 3rem; }

.pointer {
  cursor: pointer; }

.Icon {
  fill: #000000; }
  .Icon:hover, .Icon:focus {
    fill: #e61c1c; }

.red {
  fill: #e61c1c; }
  .red:hover, .red:focus {
    fill: #bf0d0d; }

.blue {
  fill: #5d66e9; }
  .blue:hover, .blue:focus {
    fill: #2f369c; }

.green {
  fill: #008c7d; }
  .green:hover, .green:focus {
    fill: #004039; }

.yellow {
  fill: #b38900; }
  .yellow:hover, .yellow:focus {
    fill: #664e00; }

.grey {
  fill: #1d2126; }
  .grey:hover, .grey:focus {
    fill: #000000; }

.sky {
  fill: #5b778c; }
  .sky:hover, .sky:focus {
    fill: #3f5d73; }

.white {
  fill: #ffffff; }
  .white:hover, .white:focus {
    fill: #ffffff; }

.black {
  fill: #000000; }
  .black:hover, .black:focus {
    fill: #000000; }
