@import url("https://rsms.me/inter/inter-ui.css");
.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

@media (prefers-reduced-motion: reduce) {
  * {
    -webkit-animation: none !important;
            animation: none !important;
    transition: none !important; } }

html {
  position: relative;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
      -ms-text-size-adjust: 100%;
          text-size-adjust: 100%;
  text-rendering: optimizeLegibility; }

html,
body {
  height: 100%;
  width: 100%;
  overflow: hidden; }

input,
textarea,
button,
select,
a,
div {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

body {
  font-family: 'Inter UI', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif; }

@supports (font-variation-settings: normal) {
  body {
    font-family: 'Inter UI var alt', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif; } }

button {
  border: none;
  cursor: pointer;
  padding: 0.75rem;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 0.5rem;
  transition: 0.15s ease-out;
  outline: none; }

.onlyIcon {
  border-radius: 50%;
  height: 48px;
  width: 48px; }

.small {
  font-size: 0.875rem;
  padding: 0.5rem; }

.large {
  font-size: 1.25rem;
  padding: 1rem; }

.fullWidth {
  width: 100%; }

.red {
  color: #ffffff;
  background: #e61c1c;
  border: 2px solid #e61c1c; }
  .red.outline {
    color: #e61c1c;
    border-color: #e61c1c;
    background: none; }
  .red:hover, .red:focus {
    color: #ffffff;
    border-color: #e61c1c;
    background: #e61c1c;
    box-shadow: 0 2px 15px rgba(230, 28, 28, 0.5); }
  .red:active {
    background: #990403;
    border-color: #990403;
    box-shadow: 0 1px 3px rgba(153, 4, 3, 0.25); }
  .red.onlyText {
    color: #e61c1c;
    border-color: transparent;
    background: none;
    box-shadow: none; }
    .red.onlyText:hover, .red.onlyText:focus {
      color: #e61c1c;
      border-color: transparent;
      background: rgba(230, 28, 28, 0.1); }
    .red.onlyText:active {
      background: #444cc2;
      background: rgba(230, 28, 28, 0.25);
      border-color: transparent; }

