@import url("https://rsms.me/inter/inter-ui.css");
.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

@media (prefers-reduced-motion: reduce) {
  * {
    animation: none !important;
    transition: none !important; } }

html {
  position: relative;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-size-adjust: 100%;
  text-rendering: optimizeLegibility; }

html,
body {
  height: 100%;
  width: 100%;
  overflow: hidden; }

input,
textarea,
button,
select,
a,
div {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

body {
  font-family: 'Inter UI', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif; }

@supports (font-variation-settings: normal) {
  body {
    font-family: 'Inter UI var alt', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif; } }

.Card {
  border: 2px solid #e7eef3;
  border-radius: 0.5rem;
  padding: 1rem;
  margin: 1rem 0;
  transition: 0.15s ease-out; }
  .Card h3 {
    margin-block-start: 0;
    margin-block-end: 0;
    font-size: 0.875rem; }
  .Card:focus {
    outline: 0; }

.clickable {
  cursor: pointer; }
  .clickable:hover, .clickable:focus {
    box-shadow: 0 2px 15px rgba(163, 194, 217, 0.5); }

.disabled {
  opacity: 0.4;
  pointer-events: none; }

.cardBadge {
  margin-bottom: 1rem; }

.cardSubtitle {
  font-size: 0.75rem; }

.cardBody {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.cardBodyText {
  flex: 1; }

.complementaryInfo {
  display: block;
  font-size: 0.875rem;
  margin-top: 1rem; }

