@import url("https://rsms.me/inter/inter-ui.css");
.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

@media (prefers-reduced-motion: reduce) {
  * {
    -webkit-animation: none !important;
            animation: none !important;
    transition: none !important; } }

html {
  position: relative;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
      -ms-text-size-adjust: 100%;
          text-size-adjust: 100%;
  text-rendering: optimizeLegibility; }

html,
body {
  height: 100%;
  width: 100%;
  overflow: hidden; }

input,
textarea,
button,
select,
a,
div {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

body {
  font-family: 'Inter UI', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif; }

@supports (font-variation-settings: normal) {
  body {
    font-family: 'Inter UI var alt', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif; } }

@-webkit-keyframes animation-color {
  0% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8); }
  50% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

@keyframes animation-color {
  0% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8); }
  50% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

.Timetable {
  border-spacing: 0.5rem;
  width: 100%; }
  .Timetable tbody tr {
    height: 4rem; }
  .Timetable th[scope='row'] {
    text-align: right;
    width: 3rem; }
  .Timetable td {
    background: #e7eef3;
    border-radius: 0.5rem;
    width: 100px; }

.classContent {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .classContent .contentTitle {
    font-size: 0.875rem;
    text-transform: capitalize; }
  .classContent .contentSubtitle {
    font-size: 0.75rem; }

td.red {
  background: #e61c1c;
  color: #ffffff;
  text-align: center;
  cursor: pointer;
  transition: 0.3s ease-out;
  position: relative;
  overflow: hidden;
  -webkit-animation: animation-color 0.3s forwards;
          animation: animation-color 0.3s forwards; }
  td.red .deleteIcon {
    top: 50%;
    left: 50%;
    -webkit-transform: translate3d(-50%, 100%, 0);
            transform: translate3d(-50%, 100%, 0);
    position: absolute;
    z-index: 2;
    opacity: 0;
    transition: all 0.3s ease-out; }
  td.red::after {
    content: ' ';
    transition: background 0.3s ease-out; }
  td.red:hover, td.red:focus {
    box-shadow: 0 8px 40px rgba(230, 28, 28, 0.8);
    z-index: 1; }
    td.red:hover .deleteIcon, td.red:focus .deleteIcon {
      opacity: 1;
      -webkit-transform: translate3d(-50%, -50%, 0);
              transform: translate3d(-50%, -50%, 0); }
    td.red:hover::after, td.red:focus::after {
      content: ' ';
      background: rgba(230, 28, 28, 0.8);
      width: 100%;
      display: flex;
      height: 100%;
      justify-content: center;
      align-items: center;
      position: absolute;
      border-radius: 0.5rem;
      top: 0; }

